.SortInfo {
  width: 80%;
  padding: 10px;
  margin: 5px auto;
  background-color: #0f0e13;
  background-image: 
    radial-gradient(at 0% 100%, hsla(253,16%,7%,1) 0, transparent 50%), 
    radial-gradient(at 50% 0%, hsla(225,39%,25%,1) 0, transparent 50%);
}

.SortInfo h1 {
  margin: 10px 0;
}

.SoftInfo__Content {
  display: flex;
  flex-flow: row wrap;
  text-align: left;
}

.SortInfo__Description,
.SortInfo__Case {
  padding: 7px;
  font-size: 16px;
}

.SortInfo__Description {
  flex: 2;
  margin-right: 15px;
}

.SortInfo__Case {
  flex: 1;
  padding-left: 10px;
  border-left: 1px solid #a3a3a3;
}

.SortInfo__Case h3 {
  margin-bottom: 10px;
}

.SortInfo__Case td {
  padding-bottom: 7px;
}

.SortInfo__Case code {
  padding-left: 5px;
  font-weight: 500;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .SortInfo {
    width: 90%;
  }

  .SoftInfo__Content {
    flex-flow: column nowrap;
    text-align: left;
  }

  .SortInfo__Case {
    padding: 10px;
    border-left: none;
    border-top: 1px solid #a3a3a3;
  }
}
